@import url(https://fonts.googleapis.com/css2?family=Alef&family=Kumar+One+Outline&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alef&family=Kumar+One+Outline&display=swap);
.card{transition:.5s all ease;position:relative;width:308px;height:450px;background-size:cover;border-radius:25px;overflow:hidden}.card:hover{box-shadow:0 19px 38px rgba(98,95,95,0.3),0 15px 12px rgba(98,95,95,0.22)}.card:hover .card__name{top:20px}.card__name{transition:.5s all ease;position:absolute;display:flex;flex-direction:column-reverse;justify-content:center;align-items:center;width:80%;left:10%;top:100%;height:80%;background-color:rgba(0,0,0,0.3);border-top-left-radius:25px;border-top-right-radius:25px}.card__name h2{margin-top:15px;margin-bottom:5px;font-family:"Kumar One Outline",cursive;color:white}.card__name h3{margin:0;padding:0;text-transform:capitalize;color:white}.card__ability{transition:.5s all ease;position:absolute;bottom:0;padding:0 10px;display:flex;justify-content:space-between;align-items:center;height:70px;width:100%;background-color:#000;border-bottom-left-radius:23px;border-bottom-right-radius:23px}.card__ability-img{width:50px;height:50px}.card__ability-img:hover{width:60px;height:60px}

.logo{display:flex;align-items:center;justify-content:center;grid-gap:10px;gap:10px;color:#FFFFFF;margin:0}

.searchBar{width:100%;height:10vh;min-height:60px;display:flex;justify-content:space-between;align-items:center;grid-gap:30px;gap:30px;margin-bottom:20px}.searchBar__input{flex:1 1}.searchBar__logout{display:flex;flex-direction:column;color:white}@media screen and (max-width: 555px){.searchBar{flex-direction:column;align-items:start;padding-top:20px}.searchBar__input{width:100%}.searchBar__logout{position:absolute;right:10px;top:10px}}

.containerCollection{width:100%;display:flex;flex-direction:column;justify-content:space-between;align-items:center;height:100vh;grid-gap:10px;gap:10px;padding-bottom:15px}@media screen and (max-width: 555px){.containerCollection{grid-gap:30px;gap:30px}}.containerCollection .pagination{background:rgba(255,255,255,0.3);display:flex;justify-content:center;align-items:center;width:100%;flex:1 1}.cardsBlock{transition:.5s all ease;display:grid;grid-template-columns:repeat(3, 1fr);grid-row:auto;grid-gap:25px;overflow-y:scroll;flex:10 1}@media screen and (max-width: 984px){.cardsBlock{grid-template-columns:repeat(2, 1fr)}}@media screen and (max-width: 651px){.cardsBlock{grid-template-columns:repeat(1, 1fr)}}

.description{flex:1 1;display:flex;grid-gap:20px;gap:20px}@media screen and (max-width: 728px){.description{flex-direction:column;align-items:center;padding-bottom:20px}}.description__ability{flex:1 1;display:flex;flex-direction:column;justify-content:space-evenly;background-color:rgba(0,0,0,0.4);border-radius:25px;padding:0 10px}.description__ability--information{display:flex;align-items:center;grid-gap:20px;gap:20px;color:white;padding-top:10px;padding-bottom:10px}.description__ability--information .info{flex:1 1}.description__ability--information .info h3{color:white;margin-bottom:10px}.description__ability--information .info p{padding:0;margin:0}.description__image{text-align:center}.description__image h2{color:#FFFFFF;font-size:30px}.description__image img{border-radius:25px}.lore h2{color:#FFFFFF;text-align:center;font-size:30px}.lore p{color:#FFFFFF;padding:0 20px;font-size:20px}

.block__slider--tittle{color:#FFFFFF;font-size:30px;text-align:center}.block__slider .slider{width:90%;margin:0 auto}.block__slider .slider__item{position:relative}.block__slider .slider__item img{width:100%;border-radius:45px}.block__slider .slider__item h3{width:100%;position:absolute;display:block;text-align:center;color:#FFFFFF;background-color:rgba(0,0,0,0.6);border-radius:45px;bottom:0;font-size:3rem}@media screen and (max-width: 550px){.block__slider .slider__item h3{font-size:1.5rem}}

.championPage{display:flex;height:100vh;grid-gap:10px;gap:10px;flex-direction:column}.championPage__header{padding-top:10px;display:flex}.championPage__info{flex:1 1;overflow-y:scroll}

