nsition:250ms}30%{background:url(/project/static/media/bg4.ee2a228d.webp) no-repeat top center;background-size:cover;transition:250ms}40%{background:url(/project/static/media/bg5.08ae840b.webp) no-repeat top center;background-size:cover;transition:250ms}50%{background:url(/project/static/media/bg6.e2412d79.webp) no-repeat top center;background-size:cover;transition:250ms}60%{background:url(/project/static/media/bg7.d7db34ac.webp) no-repeat top center;background-size:cover;transition:250ms}70%{background:url(/project/static/media/bg5.08ae840b.webp) no-repeat top center;background-size:cover;transition:250ms}80%{background:url(/project/static/media/bg8.df5fa8e0.webp) no-repeat top center;background-size:cover;transition:250ms}90%{background:url(/project/static/media/bg2.697c88bc.webp) no-repeat top center;background-size:cover;transition:250ms}100%{background:url(/project/static/media/bg1.35762b7b.webp) no-repeat top center;background-size:cover;transition:250ms}}

.authForm{display:flex;flex-direction:column;text-align:left;padding:25px;border-radius:15px;background-color:rgba(41,44,49,0.7);box-shadow:0 19px 38px rgba(0,0,0,0.3),0 15px 12px rgba(0,0,0,0.22)}.authForm h2{color:white}.authForm .ui.form .field>label{color:white}.authForm .btnSwitch{margin-top:15px;align-self:end}.loader{position:absolute;z-index:1000;top:0;left:0;width:100vw;height:100vh;display:flex;justify-content:center;align-items:center;background-color:rgba(0,0,0,0.5)}

.AuthPage{width:100vw;height:100vh;background:#535AFF url(/project/static/media/bg.d3f157be.png) no-repeat;background-size:cover;display:flex;justify-content:center;align-items:center}

